import Vue from 'vue'
import VueI18n from 'vue-i18n'
import zhTW from 'vee-validate/dist/locale/zh_TW.json'

Vue.use(VueI18n)

const i18n = new VueI18n({
  locale: 'zh_TW',
  messages: {
    zh_TW: {
      fields: {
        email: '',
        password: '',
        username: '',
        tel: '',
        address: '',
        comment: '',
        country: '',
        zone: '[]',
        mobile: ''
      },
      validation: zhTW.messages
    }
  },
  silentTranslationWarn: true
})

export { i18n }
